.bgLight{
  background-color: #fff !important;
}
.main {
  background-color: #4c5aaf;
  position: relative;

}
 .top {
  position: absolute;
  top: 0;
  right: 0;
}
.down {
  position: absolute;
  bottom: 0;
  left: 0;
 
}
a{
  text-decoration: none;
}

 form label{
  color: #51BFAD;
  margin-right: 8px;
}
 input{
 border:1px solid #ccc ;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  margin-top: -12px;
}
.error{
  border-color: red ;
}
.invalid {
  color: red;
}


 form input:focus{
  outline: none !important;
}
 form input[type=checkbox]{
  background-color: #5260AB !important
}
.btnPrimary{
  color:#fff !important;
background-color: #5260AB !important;
border-color: #5260AB;
}

 .social a{

 border:1px solid #ccc ;
 border-radius: 50px;
 -webkit-border-radius: 50px;
 -moz-border-radius: 50px;
 -ms-border-radius: 50px;
 -o-border-radius: 50px;
}
 .social a:nth-of-type(1){
  color: #1DA1F2;
}
.social a:nth-of-type(2){
  color:#3C5A99
}
 .signup p{
font-weight: 500;
font-size: 16px;
}
.signup p:nth-of-type(1) a:nth-of-type(1){
color: #303A6C;
}
.signup p:nth-of-type(1) a:nth-of-type(2) ,.main .signup p:nth-of-type(2) a {
  color: #51BFAD;
}
@media (max-width:786px) {
.social {
    flex-direction: column;
  }
}